import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Spin, Button } from "antd";
import { Share } from "./Share";
import AdSense from "react-adsense";

const resultInfo = [
  {
    status: " ",
    message: "  ?",
    percent: "100%",
  },
  {
    status: " ",
    message: "  !",
    percent: "60%",
  },
  {
    status: " ",
    message: "    !",
    percent: "40%",
  },
  {
    status: "",
    message: "  !",
    percent: "20%",
  },
  {
    status: " ",
    message: "     !",
    percent: "5%",
  },
  {
    status: " ",
    message: "     1%!",
    percent: "1%",
  },
  {
    status: "",
    message:
      "  100       .",
    percent: "0.1%",
  },
];

export const Result = () => {
  const location = useLocation();
  const [isLoading, setIsLoading] = useState(true);
  const [r, setR] = useState(resultInfo[0]);
  const { level } = location.state;
  useEffect(() => {
    if (level >= 1 && level < 10) {
      setR(resultInfo[0]);
    } else if (level >= 11 && level <= 20) {
      setR(resultInfo[1]);
    } else if (level >= 21 && level <= 30) {
      setR(resultInfo[2]);
    } else if (level >= 31 && level <= 40) {
      setR(resultInfo[3]);
    } else if (level >= 41 && level <= 50) {
      setR(resultInfo[4]);
    } else if (level >= 51 && level < 60) {
      setR(resultInfo[5]);
    } else if (level >= 60) {
      setR(resultInfo[6]);
    }
    setTimeout(() => {
      setIsLoading(false);
    }, 3000);
  }, [level]);
  return (
    <div className="flex justify-center flex-col">
      {isLoading ? (
        <>
          <h1 className="text-3xl text-center  pt-16">
              .
          </h1>
          <div className="text-center pt-4 mb-2">
            <Spin size="large" />
          </div>
          <AdSense.Google
            style={{ display: "block" }}
            client="ca-pub-9130836798889522"
            slot="1187196532"
            format="auto"
            responsive="true"
          />
        </>
      ) : (
        <>
          <h1 className="text-3xl text-bold text-center  pt-16">
            <strong> {r.percent}</strong>
          </h1>
          <div className="text-center text-xl pt-4 mb-2">
            <strong>{level}</strong> . {r.message}
          </div>

          <AdSense.Google
            style={{ display: "block" }}
            client="ca-pub-9130836798889522"
            slot="6547334423"
            format="auto"
            responsive="true"
          />
          <div className="text-center mb-2">
              .
          </div>
          <Share />
          <div className="text-center pb-4">
            <Button
              className="mt-2 mb-2"
              style={{ width: 336 }}
              type="primary"
              size="large"
            >
              <a href="https://mbti.inforever.xyz">MBTI  </a>
            </Button>
            <Button
              className="mt-2 mb-2"
              style={{ width: 336 }}
              type="primary"
              size="large"
            >
              <a href="https://color-age.inforever.xyz"> </a>
            </Button>
            <Button
              className="mt-2 mb-2"
              style={{ width: 336 }}
              type="primary"
              size="large"
            >
              <a href="https://animal.inforever.xyz"> </a>
            </Button>
            <Button
              className="mt-2 mb-2"
              style={{ width: 336 }}
              type="primary"
              size="large"
            >
              <a href="https://play.google.com/store/apps/details?id=com.f5game.mbti">
                MBTI 
              </a>
            </Button>
            <Button
              className="mt-2"
              style={{ width: 336 }}
              type="primary"
              size="large"
            >
              <a href="https://play.google.com/store/apps/details?id=com.f5game.today">
                 
              </a>
            </Button>
          </div>
        </>
      )}
    </div>
  );
};
